import { createPopper } from '@popperjs/core';

const btn = document.querySelector('.feedback__tooltip-btn');
const txt = document.querySelector('.feedback__tooltip-txt');

const popperInstance = createPopper(btn, txt, {
  placement: 'top',
  modifiers: [
    {
      name: 'offset',
      options: {
        offset: [5, 11],
      },
    },
  ],
});

function show() {
  // make the tooltip visible
  txt.setAttribute('data-show', '');

  // enable the event listeners
  popperInstance.setOptions((options) => ({
    ...options,
    modifiers: [
      ...options.modifiers,
      { name: 'eventListeners', enabled: true },
    ],
  }));

  // update its position
  popperInstance.update();
};

function hide() {
  // hide the tooltip
  txt.removeAttribute('data-show');

  // disable the event listeners
  popperInstance.setOptions((options) => ({
    ...options,
    modifiers: [
      ...options.modifiers,
      { name: 'eventListeners', enabled: false },
    ],
  }));
};

// --- tooltip will show when hovering or focusing the button ---

const showEvents = ['mouseenter', 'focus'];
const hideEvents = ['mouseleave', 'blur'];

showEvents.forEach((event) => {
  btn.addEventListener(event, show);
});

hideEvents.forEach((event) => {
  btn.addEventListener(event, hide);
});
