// --- define HTML elements ---
const formBtn = document.getElementById('feedback-btn');
const formName = document.getElementById('feedback-name');
const formPhone = document.getElementById('feedback-phone');
const formEmail = document.getElementById('feedback-email');
const formCheck = document.getElementById('feedback-checkbox');

formBtn.addEventListener('click', () => {
  // --- check the name input ---
  if (formName.classList.contains('just-validate-error-field') || formName.value === '') {
    formName.classList.add('shake');
    setTimeout(function() {
      formName.classList.remove('shake');
    }, 600);
  };

  // --- check the e-mail input ---
  if (formEmail.classList.contains('just-validate-error-field') || formEmail.value === '') {
    formEmail.classList.add('shake');
    setTimeout(function () {
      formEmail.classList.remove('shake');
    }, 600);
  };

  // --- check the phone input ---
  if (formPhone.classList.contains('just-validate-error-field') || formPhone.value === '') {
    formPhone.classList.add('shake');
    setTimeout(function() {
      formPhone.classList.remove('shake');
    }, 600);
  };

  // --- check the checkbox input ---
  if (formCheck.classList.contains('just-validate-error-field') || formCheck.classList.contains('just-validate-success-field') === false) {
    formBtn.classList.add('shake');
    setTimeout(function() {
      formBtn.classList.remove('shake');
    }, 600);
  };
});
