import Swiper from 'swiper';
import { Navigation, Pagination } from 'swiper/modules';

Swiper.use( [Navigation, Pagination] );

// ----------------------------
// --- OFFER SECTION SLIDER ---
// ----------------------------

const swiper_offer = new Swiper('.swiper-offer', {
  slidesPerView: 1,
  spaceBetween: 0,
  speed: 1200,
  watchOverflow: true,
  navigation: {
    nextEl: '.offer-btn-next',
    prevEl: '.offer-btn-prev',
  },
  keyboard: {
    enabled: true,
    onlyInViewport: false,
    pageUpDown: true,
  },
  breakpoints: {
    320: {
      enabled: false, // turn off the slider
    },
    577: {
      enabled: true, // turn on the slider
      slidesPerView: 2,
      slidesPerGroup: 1,
      spaceBetween: 32,
    },
    930: {
      slidesPerView: 3,
    },
    1300: {
      slidesPerView: 4,
      spaceBetween: 32,
    },
    1441: {
      slidesPerView: 'auto',
      slidesPerGroup: 3,
      spaceBetween: 32,
    },
  },
});

// ------------------------------
// --- ARTICLE SECTION SLIDER ---
// ------------------------------

const swiper_article = new Swiper('.swiper-article', {
  slidesPerView: 1,
  slidesPerGroup: 1,
  spaceBetween: 0,
  speed: 800,
  watchOverflow: true,
  navigation: {
    nextEl: '.article-btn-next',
    prevEl: '.article-btn-prev',
  },
  keyboard: {
    enabled: true,
    onlyInViewport: false,
    pageUpDown: true,
  },
  breakpoints: {
    320: {
      enabled: false, // turn off the slider
    },
    577: {
      enabled: true, // turn on the slider
      spaceBetween: 32,
    },
    760: {
      slidesPerView: 2,
    },
    930: {
      slidesPerView: 3,
    },
    1025: {
      slidesPerView: 2,
      spaceBetween: 32,
    },
  },
});
