import JustValidate from 'just-validate';
import Inputmask from "../../../node_modules/inputmask/dist/inputmask.es6.js";

// --- define HTML elements ---
const validation = new JustValidate('#feedback-form');
const telSelector = document.querySelector('input[type="tel"]');
const inputMask = new Inputmask('+7 (999) 999-99-99');
inputMask.mask(telSelector);

validation
  .addField('#feedback-name', [
    {
      rule: 'required',
      value: true,
      errorMessage: '  ',
    },
    {
      rule: 'minLength',
      value: 3,
      errorMessage: '  ',
    },
    {
      rule: 'maxLength',
      value: 30,
      errorMessage: '  ',
    },
  ])
  .addField('#feedback-email', [
    {
      rule: 'required',
      value: true,
      errorMessage: '  ',
    },
    {
      rule: 'email',
      errorMessage: ' ',
    },
  ])
  .addField('#feedback-phone', [
    {
      rule: 'required',
      errorMessage: '   ',
    },
    {
      rule: 'function',
      validator: function() {
        const phone = telSelector.inputmask.unmaskedvalue();
        return phone.length === 10
      },
      errorMessage: '  ',
    },
  ])
  .addField('#feedback-checkbox', [
    {
      rule: 'required',
      errorMessage: '   ',
    },
  ]);
